// Background
canvas {
  position: fixed;
  display: block;
  width: 100%;
  min-height: 100%;
  top: 0;
  right: 0;
  bottom: 10px;
  left: 0;
  z-index: -1; }


@import './styles/menu';

$mainColor : #01686B;
$accentColor: #F46060;
$mainFont: 'Syne', sans-serif;
$secondaryFont : 'Bebas Neue', cursive;

$screen-sm-min: 576px;
$screen-md-min: 768px;
$screen-lg-min: 992px;
$screen-xl-min: 1200px;
@mixin sm {
  @media (min-width: #{$screen-sm-min}) {
    @content; } }
@mixin md {
  @media (min-width: #{$screen-md-min}) {
    @content; } }
@mixin lg {
  @media (min-width: #{$screen-lg-min}) {
    @content; } }
@mixin xl {
  @media (min-width: #{$screen-xl-min}) {
    @content; } }
@mixin rwd($screen) {
  @media (min-width: $screen+'px' ) {
    @content; } }



#root {
  overflow: hidden;
  min-height: 100vh; }
#boutonInstall {
  width: 100%; }
body {
  padding-bottom: 3rem; }

// General
.hrCouleur {
  border: 1px solid $mainColor;
  opacity: .4; }


.accentColorText {
  color: $accentColor; }

button {
  background-color: $accentColor!important; }

// Acceuil
#imageIndex {
  img {
    padding: .7rem; } }

.hidebeforemd {
  display: none;
  @include md {
    display: grid; } }

// Affichage des librairies
.card {
  background-image: url('https://i.pinimg.com/originals/e3/21/6b/e3216b575d5e92031eaf5c1a067f9d31.jpg');
  background-size: cover; }

// Formulaire
input {
  opacity: .4; }

label {
  font-size: 2rem;
  font-family: $mainFont; }
.titrePage {
  font-family: $secondaryFont;
  font-size: 3rem; }
.apercu {
  display: none;
  @include md {
    display: grid; } }

// Footer
.footer {
  background-color: rgba(102, 16, 31,0.6);
  position: fixed!important;
  height: 3rem;
  right: 0px;
  left: 0;
  bottom: 0;

  h4 {
    color: #FFE3DC;
    font-size: 1rem;
    @include lg {
      font-size: 2rem; } } }


